/* eslint-disable react-hooks/exhaustive-deps */
import {
  UnsupportedChainIdError
  , useWeb3React
} from "@web3-react/core";
import React, {
  useRef
  , useEffect
  , useState
} from "react";
import MetaMaskLogo from "../../assets/images/metamask.png"

import WalletConnectLogo from "../../assets/images/wallet-Connect.png";
import {
  injected
  , walletconnect
} from "../../connectors";
import MetaMaskOnboarding from "@metamask/onboarding";
// import { toast } from "react-toastify";
import Modal from "./Modal";

const ConnectWallet = (props) => {
  
  // console.log("in modal wallet");
  
  const {
      close
      , isOpen
  } = props;
  const {
      account
      , activate
      , error
      , connector
  } = useWeb3React();
  const [activatingConnector, setActivatingConnector] = useState();
  const onboarding = useRef();
  
  const onConnectWithMetamaskClick = () => {
      if (MetaMaskOnboarding.isMetaMaskInstalled()) {
          setActivatingConnector(injected);
          activate(injected, undefined, true)
              .then(() => {
                  alert("Connected successfully");
              })
              .catch(() => {
                  // setTried(true);
              });
          localStorage.setItem("connectedWallet", "metamask");
      } else {
          onboarding.current.startOnboarding();
      }
  };
  
  const onConnectWithWalletConnect = () => {
      setActivatingConnector(walletconnect);
      activate(walletconnect, undefined, true)
          .catch((err) => {
              setActivatingConnector();
              walletconnect.walletConnectProvider = undefined;
              localStorage.removeItem("connectedWallet");
              if (err) {
                  window.location.reload(false);
              }
          });
      localStorage.setItem("connectedWallet", "walletConnect");
  };
  
  useEffect(() => {
      if (!onboarding.current) {
          onboarding.current = new MetaMaskOnboarding();
      }
  }, []);
  
  useEffect(() => {
      if (MetaMaskOnboarding.isMetaMaskInstalled()) {
          if (account && account.length > 0) {
              onboarding.current.stopOnboarding();
          }
      }
  }, [account]);
  
  useEffect(() => {
      if (activatingConnector && activatingConnector === connector) {
          setActivatingConnector(undefined);
      }
  }, [activatingConnector, connector]);
  
  useEffect(() => {
      if (MetaMaskOnboarding.isMetaMaskInstalled()) {
          if (account && account.length > 0) {
              onboarding.current.stopOnboarding();
          }
      }
  }, [account]);
  
  useEffect(() => {
      if (error instanceof UnsupportedChainIdError) {
          alert("Please connect to the Binance Smart Chain");
      }
  }, [error]);
  
  return (
      <Modal closeModal={close} isOpen={isOpen}>

        {window.ethereum !== undefined ?  (
    <div className="cjAFRf">
      <div className="jcNZzC" onClick={onConnectWithMetamaskClick}>
        <div className="jMhaxE">
          <img
            src={MetaMaskLogo}
            alt="MetaMask"
            className="wallet_connect_img"
          />
        </div>
        <div className="bktcUM">MetaMask</div>
      </div>
    </div>
        ):  ''}
    <div
      className="cjAFRf"
      style={{
        borderTop: "1px solid rgba(195, 195, 195, 0.56)",
      }}
    >
      <div className="jcNZzC" onClick={onConnectWithWalletConnect}>
        <div className="jMhaxE">
          <img
            src={WalletConnectLogo}
            alt="WalletConnect"
            className="wallet_connect_img"
          />
        </div>
        <div className="bktcUM">WalletConnect</div>
      </div>
    </div>
  </Modal>
  );
};

export default ConnectWallet;
