import React, { useEffect, useState } from 'react'
import box1 from "../assets/images/1.png";
import box2 from "../assets/images/2.png";
import box3 from "../assets/images/3.png";
import box4 from "../assets/images/5right.png";
import Animatedlogo from '../components/common/Animatedlogo'
import './Maindashboard.css'
import Navbar from '../components/common/Navbar'
import { getUserDetailService } from '../services/userServices';
import Loader from './Loader';
import { useDispatch, useSelector } from 'react-redux';

export default function Maindashboard() {

  const getTokenData = useSelector(state => state.tokenReducer)
  const dispatch = useDispatch()

  //states
  const [userDetails, setUserDetails] = useState({})
  const [zepXinOneUSDT, setZepXinOneUSDT] = useState(10000)
  const [isLoading, setIsLoading] = useState(false);

  //function to get user details
  const getUserDetails = async () => {

    try {
      setIsLoading(true)
      let res = await getUserDetailService(getTokenData.token)
      dispatch({
        type: 'SET_REFERRAL_ID',
        payload: res.data.result.ownReferralCode
      })
      dispatch({
        type: 'SET_WALLET_AMOUNT',
        payload: res.data.result.walletAmount
      })
      setUserDetails(res.data.result)
      setIsLoading(false)
    } catch (error) {
      setIsLoading(false)
      console.log(error)
    }
  }


  useEffect(() => {
    getUserDetails()
  }, []);

  return (
    <>
      {
        !isLoading ?
          <div className="main__dashboard">



            <div className='main__dashboard__boxes'>

              <div className="main__dashboard__leftBoxes">

                <div className="main__dashboard__leftBoxes__box1">
                  <div className="main__dashboard__leftBox__details">
                    <p className="main__dashboard__leftBox__heading">ZEPX</p>
                    <p className='main__dashboard__leftBox__count'>{userDetails?.joiningDetails?.joiningAmount ? userDetails?.joiningDetails?.joiningAmount * zepXinOneUSDT : 0}</p>
                  </div>
                </div>

                <div className="main__dashboard__leftBoxes__box2">
                  <div className="main__dashboard__leftBox__details">
                    <p className="main__dashboard__leftBox__heading">Total Members</p>
                    <p className='main__dashboard__leftBox__count'>{userDetails?.totalTeamMembers}</p>
                  </div>
                </div>

                <div className="main__dashboard__leftBoxes__box3">
                  <div className="main__dashboard__leftBox__details">
                    <p className="main__dashboard__leftBox__heading">Downline MEMBERS</p>
                    <p className='main__dashboard__leftBox__count'>{userDetails?.downlineMembers}</p>
                  </div>
                </div>


              </div>

              <div className="main__dashboard__middleArea">

                <Animatedlogo className='main__dashboard__middleAreaLogo' />

                <div className="main__dashboard__middleArea__box4">
                  <div className="main__dashboard__middleArea__details">
                    <p className="main__dashboard__middleArea__heading">REFERED MEMBERS</p>
                    <p>{userDetails?.totalTeamMembers ? userDetails.totalTeamMembers - userDetails.downlineMembers : 0}</p>
                  </div>
                </div>
              </div>


              <div className="main__dashboard__rightBoxes">

                <div className="main__dashboard__rightBoxes__box5">
                  <div className="main__dashboard__rightBox__details">
                    <p className="main__dashboard__rightBox__heading">USDT</p>
                    <p className='main__dashboard__rightBox__count'>{userDetails?.joiningDetails?.joiningAmount ? userDetails?.joiningDetails?.joiningAmount : 0}</p>
                  </div>
                </div>

                <div className="main__dashboard__rightBoxes__box6">
                  <div className="main__dashboard__rightBox__details">
                    <p className="main__dashboard__rightBox__heading">Total Income</p>
                    <p className='main__dashboard__rightBox__count'>{userDetails?.walletAmount ? userDetails?.walletAmount : 0}</p>
                  </div>
                </div>

                <div className="main__dashboard__rightBoxes__box7">
                  <div className="main__dashboard__rightBox__details">
                    <p className="main__dashboard__rightBox__heading">Direct Income</p>
                    <p className='main__dashboard__rightBox__count'>{userDetails?.directIncome ? userDetails?.directIncome : 0}</p>
                  </div>
                </div>




              </div>

            </div>

          </div>
          :
          <Loader />
      }
    </>

  )
}
