import React, { useEffect, useState } from "react";
import animatedLogo from "../assets/images/Gif-Logo-1.gif";
import { getRootIdByReferralCodeService, registerUserService } from "../services/AuthServices";
import "./Register.css";
import {
  useNavigate,
} from "react-router-dom";
import Loader from "./Loader";

export default function Register() {

  const navigate = useNavigate();

  const queryParams = new URLSearchParams(window.location.search);
  const referralCode = queryParams.get('referralCode');

  // States
  const [email, setEmail] = useState('')
  const [referralId, setReferralId] = useState('')
  const [parentId, setParentId] = useState('')
  const [rootId, setRootId] = useState('')
  const [selectedPlan, setSelectedPlan] = useState('')
  const [totalJoiningAmount, setTotalJoiningAmount] = useState('')
  const [password, setPassword] = useState('')
  const [isLoading, setIsLoading] = useState(false);

  const validateData = () => {
    try {
      if (email && referralId && parentId && selectedPlan && totalJoiningAmount && password) {
        return true
      }
      else {
        return false
      }
    } catch (error) {

    }
  }

  const registerButtonHandler = async () => {
    try {
      if (validateData()) {
        setIsLoading(true)
        let dataToSend = {}
        dataToSend.email = email
        dataToSend.referralCode = referralId
        dataToSend.parentReferralCode = parentId
        dataToSend.selectedPlan = selectedPlan
        dataToSend.password = password
        dataToSend.joiningAmount = totalJoiningAmount
        let res = await registerUserService(dataToSend)
        navigate("/payment", {
          state: {
            email: email,
            amount: totalJoiningAmount
          }
        })
        setIsLoading(false)
      }
      else {
        alert('Fill all required fields.')
        setIsLoading(false)
      }
    } catch (error) {
      setIsLoading(false)
      alert('Failed To Register:' + error.response.data.error)

    }
  }

  const getRootReferralCodeByReferralCode = async (referralId) => {
    try {
      let res = await getRootIdByReferralCodeService(referralId)
      setRootId(res.data.result.rootReferralCode)
    } catch (error) {
      console.log(error)
    }
  }

  const calculateJoiningAmount = () => {
    try {
      let i = parseInt(selectedPlan)
      let res = 0
      while (i >= 25) {
        res = res + i
        i = i / 2
      }
      setTotalJoiningAmount(res)
    } catch (error) {

    }
  }

  useEffect(() => {
    calculateJoiningAmount()
    if (referralCode) {
      setReferralId(referralCode)
    }
  }, [selectedPlan])

  return (
    <div className="register">
      <img src={animatedLogo} className="register__logo" />

      <div className="register__headings">
        <h3 className="register__headings__1">Sign Up</h3>
        <h3 className="register__headings__2">
          Enter Your details
        </h3>
      </div>

      <div className="register__inputBox">
        <div className="register__inputBox__mainbox">
          <div className="register__inputBox__left">
            <input value={email} onChange={(e) => { setEmail(e.target.value) }} type="Email" placeholder="Email" />
            <input value={referralId} onChange={(e) => { setReferralId(e.target.value) }} onBlur={() => { getRootReferralCodeByReferralCode(referralId) }} type="text" placeholder="Referral ID" />
            <input value={parentId} onChange={(e) => { setParentId(e.target.value) }} type="text" placeholder="Parent Id" />
          </div>

          <div className="register__inputBox__right">
            <select value={selectedPlan} onChange={(e) => { setSelectedPlan(e.target.value) }} class="register__inputBox__right__select">
              <option>Select Plans</option>
              <option value="25">$25</option>
              <option value="50">$50</option>
              <option value="100">$100</option>
              <option value="200">$200</option>
              <option value="400">$400</option>
              <option value="800">$800</option>
              <option value="1600">$1600</option>
              <option value="3200">$3200</option>
              <option value="6400">$6400</option>
              <option value="12800">$12800</option>
              <option value="25600">$25600</option>
              <option value="51200">$51200</option>
            </select>
            <input disabled value={`$` + totalJoiningAmount} type="text" placeholder="Total Joining Amount" />
            <input value={password} onChange={(e) => { setPassword(e.target.value) }} type="password" placeholder="Enter Password" />
          </div>
        </div>

        <div>
          <div className="register__button__box">
            <button onClick={() => registerButtonHandler()} className="register__button__box__button">Register</button>
          </div>
        </div>
      </div>
      {isLoading && <Loader />}
    </div>
  );
}
