import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import { getBusinessHistoryService, getTransactionHistoryService } from '../../services/userServices';
import { useSelector } from 'react-redux';

const columns = [
    {
        field: 'id',
        headerName: 'SL',
        width: 90
    },
    {
        field: 'narration',
        headerName: 'Narration',
        width: 150,
    },
    {
        field: 'currency',
        headerName: 'Currency',
        width: 150,
    },
    {
        field: 'amount',
        headerName: 'Amount',
        width: 110,
    },
    {
        field: 'transactionHash',
        headerName: 'Transaction Hash',
        width: 180,
    },
    {
        field: 'date',
        headerName: 'Date',
        width: 200
    },
];

export default function TransactionHistory() {

    const getTokenData = useSelector(state => state.tokenReducer)

    const [incomeDetails, setIncomeDetails] = React.useState([])

    const getIncomeDetails = async () => {
        try {
            let res = await getTransactionHistoryService(getTokenData.token)
            let temp = []
            res.data.result.map((d, i) => {
                temp.push({
                    // id: i + 1,
                    id: (i + 1).toString(),
                    narration: (d.narration),
                    currency: d.currency,
                    amount: (d.amount),
                    transactionHash: d.transactionHash,
                    date: new Date(d.timeStamp)
                })
            })
            setIncomeDetails([...temp])
        } catch (error) {
            console.log(error)
        }
    }

    React.useEffect(() => {
        getIncomeDetails()
    }, [])


    return (
        <div>
            {incomeDetails.length > 0 && <Box sx={{ height: 550, width: '70%' }}>
                <DataGrid sx={{ backgroundColor: 'white' }}
                    rows={incomeDetails}
                    columns={columns}
                    pageSize={8}
                    rowsPerPageOptions={[8]}
                />
            </Box>}
        </div>
    );
}
