import React, { useState } from 'react'
import animatedLogo from "../assets/images/Gif-Logo-1.gif";
import { loginUserService } from '../services/AuthServices';
import './Login.css'
import {
  useNavigate,
} from "react-router-dom";
import { useDispatch } from 'react-redux';

export default function Login({ setIsLoggedIn }) {

  const dispatch = useDispatch()

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const navigate = useNavigate();

  const validateData = () => {
    try {
      if (email && password) {
        return true
      }
      else {
        return false
      }
    } catch (error) {
      console.log(error)
    }
  }

  const handleLoginButton = async () => {
    try {
      if (validateData()) {
        let dataToSend = {}
        dataToSend.userId = email
        dataToSend.password = password
        let res = await loginUserService(dataToSend)
        dispatch({
          type:'SET_TOKEN',
          payload:res.data.result.token
        })
        setIsLoggedIn(true)
        navigate("/dashboard")
      }
      else {
        alert('Fill all required fields.')
      }
    } catch (error) {
      console.log(error)
      alert('Something Went Wrong.')
    }
  }

  return (
    <div className="login">
      <img src={animatedLogo} className="login__logo" />

      <div className="login__headings">
        <h2 className="login__headings__1">Sign In</h2>
        <h3 className="login__headings__2">
          Enter Your details to login to your account
        </h3>
      </div>

      <div className='login__inputs'>
        <input value={email} onChange={(e) => { setEmail(e.target.value) }} type='email' placeholder='Enter Email ID' />
        <input value={password} onChange={(e) => { setPassword(e.target.value) }} type='password' placeholder='Enter Password' />
      </div>


      <div className='login__buttons'>
        <button onClick={() => { handleLoginButton() }} className='login__buttons__signin'>SIGN IN</button>
        <p className='login__buttons__donthaveaccount'>Don't have an account yet</p>
        <button onClick={()=>{navigate("/register")}} className='login__buttons__signup'>SIGN UP</button>

      </div>





    </div>
  )
}
