import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import { getAllTeamMemberService, getBusinessHistoryService } from '../../services/userServices';
import { useSelector } from 'react-redux';

const columns = [
  {
    field: 'sl',
    headerName: 'SL',
    width: 90
  },
  {
    field: 'id',
    headerName: 'Member Id',
    width: 150,
  },
  {
    field: 'referredBy',
    headerName: 'Referred By',
    width: 150,
  },
  {
    field: 'parentId',
    headerName: 'Parent Id'
  },
  {
    field: 'email',
    headerName: 'Email',
    width: 110,
  },
  {
    field: 'joiningAmount',
    headerName: 'Joining Amount',
    width: 110,
  },
  {
    field: 'date',
    headerName: 'Joining Date',
    width: 200
  },
];

export default function ReferrList() {

  const getTokenData = useSelector(state => state.tokenReducer)

  const [incomeDetails, setIncomeDetails] = React.useState([])

  const getIncomeDetails = async () => {
    try {
      let res = await getAllTeamMemberService(getTokenData.token)
      let temp = []
      res.data.result.map((d, i) => {
        if (d.parentReferralCode != localStorage.getItem('referralId')) {
          temp.push({
            sl: (i + 1).toString(),
            id: (d.ownReferralCode).toString(),
            referredBy: d.referredBy,
            parentId: d.parentReferralCode,
            email: d.email,
            joiningAmount: d.joiningDetails.selectedPlan.toString(),
            date: new Date(d.joiningDetails.dateTimestamp)
          })
        }
      })
      setIncomeDetails([...temp])
    } catch (error) {
      console.log(error)
    }
  }

  React.useEffect(() => {
    getIncomeDetails()
  }, [])


  return (
    <div>
      {incomeDetails.length > 0 && <Box sx={{ height: 550, width: '70%' }}>
        <DataGrid sx={{ backgroundColor: 'white' }}
          rows={incomeDetails}
          columns={columns}
          pageSize={8}
          rowsPerPageOptions={[8]}
        />
      </Box>}
    </div>
  );
}
