import { Provider } from 'react-redux'
import store from './store'
import './App.css'
import AppRoutes from './routes/AppRoutes'
import React, { useState, useEffect } from 'react'
import { useWeb3React } from '@web3-react/core'
import { useEagerConnect, useInactiveListener } from './hooks/index'
import getWeb3 from './getWeb3'

function App() {
  const [activatingConnector, setActivatingConnector] = useState()
  const { connector, active, account } = useWeb3React()

  const triedEager = useEagerConnect()

  useInactiveListener(!triedEager || !!activatingConnector)

  useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  useEffect(() => {
    if (active) localStorage.setItem('shouldEggerConnect', 'true')
  }, [active])

  useInactiveListener(!triedEager || !!activatingConnector)

  useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  useEffect(() => {
    if (active) localStorage.setItem('shouldEggerConnect', 'true')
  }, [active])

  useEffect(() => {
    const assignWeb3 = async () => {
      window.web3 = await getWeb3()
      console.log('error ', window.web3)
    }
    if (account) {
      assignWeb3()
    }
  }, [account])

  return (
    <>
      <Provider store={store}>
        <AppRoutes />
      </Provider>
    </>
  )
}

export default App
