import { Grid } from "@mui/material";
import React, { useEffect, useState } from "react";
import AllTeamList from "../components/common/AllTeamList";
import BusinessHistory from "../components/common/BusinessHistory";
import DirectIncome from "../components/common/DirectIncome";
import DirectTeamList from "../components/common/DirectTeamList";
import LevelIncome from "../components/common/LevelIncome";
import Navbar from "../components/common/Navbar";
import ReferrList from "../components/common/ReferrList";
import Responsivenavbar from "../components/common/Responsivenavbar";
import Sidebarmenu from "../components/common/Sidebarmenu";
import TransactionHistory from "../components/common/TransactionHistory";
import "./Dashboard.css";
import Maindashboard from "./Maindashboard";

export default function Dashboard() {

  const [selectedMenu, setSelectedMenu] = useState('dashboard')

  return (

    <div className="mainDashboard">
      <div className="dashboard">

        <Sidebarmenu setSelectedMenu={setSelectedMenu} />
      </div>
      <div className="main">
        {selectedMenu == 'dashboard' && [<Navbar />, <Maindashboard />]}

        {selectedMenu == 'referrList' && [<Navbar />, <ReferrList />]}

        {selectedMenu == 'directList' && [<Navbar />, <DirectTeamList />]}

        {selectedMenu == 'allTeamList' && [<Navbar />, <AllTeamList />]}

        {selectedMenu == 'totalIncome' && [<Navbar />, <BusinessHistory />]}

        {selectedMenu == 'levelIncome' && [<Navbar />, <LevelIncome />]}

        {selectedMenu == 'directIncome' && [<Navbar />, <DirectIncome />]}

        {selectedMenu == 'businessHistory' && [<Navbar />, <BusinessHistory />]}

        {selectedMenu == 'transactionHistory' && [<Navbar />, <TransactionHistory />]}

      </div>

    </div>
  );
}
