import React, { useState } from 'react'
import Avtar from '../../assets/images/avtar.png'
import './Sidebarmenu.css'
import GridViewIcon from '@mui/icons-material/GridView';
import Person2Icon from '@mui/icons-material/Person2';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import GroupsIcon from '@mui/icons-material/Groups';
import BusinessIcon from '@mui/icons-material/Business';
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';

export default function Sidebarmenu({ setSelectedMenu }) {
  const [referList, setOpenreferlist] = useState(false)
  const [incomeList, setOpenincomelist] = useState(false)

  const navigate = useNavigate();
  const userDetails = useSelector(state => state.userReducer)
  const dispatch = useDispatch()

  const handleSignOutButton = () => {
    try {
      dispatch({
        type: 'CLEAR_TOKEN'
      })
      navigate("/login")
    } catch (error) {

    }
  }

  return (
    <div className='sidebarmenu'>

      <img src={Avtar} className='sidebarmenu__avtarImg' />
      <h1 style={{ color: 'white' }}>{userDetails?.referralId}</h1>

      <div onClick={() => { setSelectedMenu('dashboard') }} className='sidebarmenu__box'>
        <GridViewIcon />
        <p className='sidebarmenu__box__headings'>Dashboard</p>
      </div>

      <div className='sidebarmenu__box'>
        <Person2Icon />
        <p className='sidebarmenu__box__headings'>Profile</p>
      </div>


      <div className='sidebarmenu__box' onClick={() => { referList ? setOpenreferlist(false) : setOpenreferlist(true) }}>
        <GroupsIcon />
        <p className='sidebarmenu__box__headings'>Team List</p>
        <ArrowDropDownIcon />
      </div>

      {referList ? <div className='sidebarmenu__box__teamList__details'>
        <p onClick={() => { setSelectedMenu('referrList') }}>Reffer List</p>
        <p onClick={() => { setSelectedMenu('directList') }}>Direct List</p>
        <p onClick={() => { setSelectedMenu('allTeamList') }}>Team List</p>
      </div> : null}





      <div className='sidebarmenu__box'>
        <BusinessIcon />
        <p onClick={() => { setSelectedMenu('businessHistory') }} className='sidebarmenu__box__headings'>Business History</p>
      </div>

      <div className='sidebarmenu__box' onClick={() => { incomeList ? setOpenincomelist(false) : setOpenincomelist(true) }}>
        <Person2Icon />
        <p className='sidebarmenu__box__headings'>Income Details</p>
        <ArrowDropDownIcon />
      </div>

      {incomeList ? <div className='sidebarmenu__box__teamList__details'>
        <p onClick={() => { setSelectedMenu('totalIncome') }}>Total Income</p>
        <p onClick={() => { setSelectedMenu('directIncome') }}>Direct Income</p>
        <p onClick={() => { setSelectedMenu('levelIncome') }}>Level Income</p>
      </div> : null}

      <div className='sidebarmenu__box'>
        <ReceiptLongIcon />
        <p onClick={() => { setSelectedMenu('transactionHistory') }} className='sidebarmenu__box__headings'>Transaction History</p>
      </div>

      <button onClick={() => { handleSignOutButton() }} className='sidebarmenu__signOutbtn'><ExitToAppIcon />Sign Out</button>



    </div>
  )
}
