import React, { useEffect, useState } from 'react'
import '../views/Payment.css'
import animatedLogo from "../assets/images/Gif-Logo-1.gif";
import {
  useLocation,
  useNavigate,
} from "react-router-dom";
import { updateJoiningInfoService } from '../services/AuthServices';
import Loader from './Loader.js';


import truncateEthAddress from 'truncate-eth-address'
import { useWeb3React } from '@web3-react/core'
import { useBUSDcontract } from '../hooks/index.js'
import { ethers } from 'ethers'
import ConnectWallet from '../components/ConnectWallet/ConnectWallet';


export default function Payment() {
  const [isLoading, setIsLoading] = useState(false);
  const { state } = useLocation();
  const navigate = useNavigate();

  const ON_BOARD_TEXT = 'PAY NOW'

  const { account } = useWeb3React()
  const [isOpen, setIsOpen] = useState()
  const [btn, setBtn] = useState(ON_BOARD_TEXT)
  const BUSDContract = useBUSDcontract(true)

  useEffect(() => {
    if (account) {
      const walletAddress = account
      let t = truncateEthAddress(walletAddress)
      setBtn(t)
    }
  }, [account])

  // const handlePayNowButton = async () => {
  //   try {
  //     setIsLoading(true);
  //     await updateJoiningDetails()
  //     setIsLoading(false);
  //   } catch (error) {
  //     setIsLoading(false);
  //   }
  // }

  const paymentHandlerforToken = async () => {
    console.log("contract",)
    if (account === undefined) {
      alert("WALLET NOT CONNECTED")
      return
    }
    console.log("contrasssssssssct",)
    console.log("account", account)

    const Usdt_Bal = await BUSDContract.methods.balanceOf(account).call()
    console.log("Usdt_Bal", Usdt_Bal)
    console.log("type", typeof state?.amount)
    console.log("value", state?.amount)
    const sendingamount = (state?.amount).toString()
    if (Usdt_Bal < state?.amount) {
      alert('NOT SUFFICENT BALANCE')
      return
    }
    try {
      console.log('Usdt_Bal', Usdt_Bal)
      setIsLoading(true)
      const transaction = await BUSDContract.methods
        .transfer(
          "0x0901e1B96817EA0832C228d27840F6dcf530213c"
          , ethers.utils.parseEther((sendingamount))
          ,)
        .send({
          /* global BigInt */
          from: account
          ,
        })
      console.log('Transaction_recipt', transaction)
      await updateJoiningDetails(transaction.transactionHash)
      setIsLoading(false)
    } catch (error) {
      console.log(error)
      setIsLoading(false)
    }

  }

  const updateJoiningDetails = async (tx) => {
    try {
      let dataToSend = {}
      dataToSend.email = state.email
      dataToSend.amount = state.amount
      dataToSend.transactionId = tx
      let res = await updateJoiningInfoService(dataToSend)
      navigate("/login")
    } catch (error) {
      alert('Something Went Wrong.')
    }
  }

  return (
    // <div className="payment">
    //   <img src={animatedLogo} className="payment__logo" />
    //   <p style={{ color: 'white' }}>Email:{state?.email}</p>
    //   <p style={{ color: 'white' }}>Total Payable Amount: {state?.amount} USDT</p>
    //   <button onClick={() => { handlePayNowButton() }} className='payment__paynow'>PAY NOW</button>
    //   {isLoading && <Loader />}
    // </div>

    <div className="payment">
      <img src={animatedLogo} className="payment__logo" />
      <p style={{ color: 'white' }}>Email:{state?.email}</p>
      <p style={{ color: 'white' }}>
        Total Payable Amount: {state?.amount} USDT
      </p>
      <ConnectWallet isOpen={isOpen} close={setIsOpen} />
      {

        account === undefined ? (
          <button onClick={() => setIsOpen(true)} className="payment__paynow">
            PAY NOW
          </button>
        ) : (
          <button
            onClick={() => paymentHandlerforToken()}
            className="payment__paynow"
          >
            {
              ON_BOARD_TEXT
            }
          </button>
        )}

      {isLoading && <Loader />}
    </div>


  )
}
