let tokenIntitialState = {
    token: null
}

export const adminTokenReducer = (state = tokenIntitialState, action) => {
    switch (action.type) {
        case 'SET_ADMIN_TOKEN':
            return {
                ...state,
                token: action.payload,
            };
        case 'CLEAR_ADMIN_TOKEN':
            return {
                ...state,
                token: null,
            };

        default:
            return state;
    }
}